import React, { Component } from 'react';
import { Link } from 'react-router';
import SVGInline from "react-svg-inline";
import Butter from 'buttercms';

const butter = Butter('11878ae74ff57a7a40869cd9fe0f547e67c52a50');

class BlogHome extends Component {

  constructor(props) {
    super(props);

    this.state = {
      loaded: false
    };
  }

  fetchPosts(page) {
    butter.post.list({page: page, page_size: 3}).then((resp) => {
      this.setState({
        loaded: true,
        resp: resp.data
      })
    });
  }

  componentWillMount() {
    let page = this.props.params.page || 1;

    this.fetchPosts(page)

  }

  componentWillReceiveProps(nextProps) {
    this.setState({loaded: false});

    let page = nextProps.params.page || 1;

    this.fetchPosts(page)
  }

  render() {
    if (this.state.loaded) {
      const { next_page, previous_page } = this.state.resp.meta;
      const { count } = this.state.resp.meta;

      if (count >= 3) {
        var HomePageBlogWrapperClass = "three ui stackable cards";
      } else {
        var HomePageBlogWrapperClass = "one ui stackable cards";
      }
 
      return (
          <div id="homepage-content">

            <div id="HomePageOpenerWrapper">
              <h1>Hello, as you can see from the title, my name is Nicholas Griffin and I am a web developer, blogger, and a complete tech nerd.</h1>
            </div>

        <div id="HomePageBlogWrapper" className={HomePageBlogWrapperClass}>
        <h1 id="BlogHomeTitle">Looking for more?</h1>
          {this.state.resp.data.map((post) => {
            return (
              <div className="ui card" key={post.slug}>
              <div className="ui image">
    <img src={post.featured_image} className="visible content" />
  </div>
                <div className="content">
                  <div className="header">{post.title}</div>
                  <div className="meta">
                    <a></a>
                  </div>
                  <p>
                  {post.summary}
                  </p>
                </div>
                <div className="extra content">
                <Link className="ui button primary basic" to={`/post/${post.slug}`}>Read More</Link>
  </div>
              </div>
            )
          })}

          <div>
            {previous_page && <Link to={`/p/${previous_page}`}>Prev</Link>}

            {next_page && <Link to={`/p/${next_page}`}>Next</Link>}
          </div>
        </div>
        <div id="contact_section">
        <h2>Send me a message</h2>

    <p>If you would like to get in touch with me, feel free to use the form below, but before you do heed this warning, I don't respond to SPAM nicely ;).</p>

    <form method="POST" className="ui form" action="https://formspree.io/responses@nicholasgriffin.co.uk">
    <div className="field">
    <label htmlFor="name">Name</label>
    <input type="text" name="name" required/>
    </div>
    <div className="field">
    <label htmlFor="email">Email</label>
    <input type="email" name="email" required/>
    </div>
    <div className="field">
    <label htmlFor="message">Your Message</label>
    <textarea name="message" rows="2" required></textarea>
  </div>

    <button className="ui button" type="submit">Submit</button>
  </form>
  </div>
        </div>
      );
    } else {
      return (
        <div className="loadingWrapper">
          <div className="loader loader--style4" title="3">
          <SVGInline svg={'<svg version="1.1" id="Layer_1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px"width="24px" height="24px" viewBox="0 0 24 24" style="enable-background:new 0 0 50 50;" xml:space="preserve"><rect x="0" y="0" width="4" height="7" fill="#333"><animateTransform  attributeType="xml" attributeName="transform" type="scale" values="1,1; 1,3; 1,1" begin="0s" dur="0.6s" repeatCount="indefinite" /></rect><rect x="10" y="0" width="4" height="7" fill="#333"> <animateTransform  attributeType="xml" attributeName="transform" type="scale" values="1,1; 1,3; 1,1" begin="0.2s" dur="0.6s" repeatCount="indefinite" /></rect><rect x="20" y="0" width="4" height="7" fill="#333"><animateTransform  attributeType="xml" attributeName="transform" type="scale" values="1,1; 1,3; 1,1" begin="0.4s" dur="0.6s" repeatCount="indefinite" /></rect></svg>'} />
</div>
        </div>
      )
    }
  }
}

export default BlogHome;


