import React, { Component } from 'react';
import Butter from 'buttercms';
import { Link } from 'react-router';
import { Helmet } from "react-helmet";
import { DiscussionEmbed } from 'disqus-react';
import SVGInline from "react-svg-inline";
import { TwitterButton, RedditButton, EmailButton } from "react-social";

const butter = Butter('11878ae74ff57a7a40869cd9fe0f547e67c52a50');

const appId = "";

class BlogPost extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      loaded: false
    };
  }

  componentWillMount() {
    let slug = this.props.params.slug;

    butter.post.retrieve(slug).then((resp) => {
      this.setState({
        loaded: true,
        post: resp.data.data
      })
    });
  }

  render() {
    if (this.state.loaded) {
      const post = this.state.post;

      const disqusShortname = 'nicholasgriffin-1';
        const disqusConfig = {
        };

      return (
        <div>
          <Helmet>
            <title>{post.seo_title}</title>
            <meta name="description" content={post.meta_description} />
            <meta name="og:image" content={post.featured_image} />
          </Helmet>

          <div className="container">

          <div className="ui secondary  menu">
          <Link className="item" to={`/`}>
    Home
  </Link>
  <div className="right menu">
  <TwitterButton element="a" className="item" message={post.seo_title}>
  <i class="twitter square icon"></i>
      </TwitterButton>
  <RedditButton element="a" className="item" title={post.seo_title}>
  <i class="reddit square icon"></i>
      </RedditButton>
  <EmailButton element="a" className="item" message={post.seo_title}>
  <i class="envelope square icon"></i>
      </EmailButton>
      </div>
</div>

          <h1>{post.title}</h1>
          <div dangerouslySetInnerHTML={{__html: post.body}} />
          <div className="article_comments">
            <DiscussionEmbed shortname={disqusShortname} config={disqusConfig} />
        </div>
        </div>
        </div>
      );
    } else {
      return (
        <div className="loadingWrapper">
          <div className="loader loader--style4" title="3">
          <SVGInline svg={'<svg version="1.1" id="Layer_1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px"width="24px" height="24px" viewBox="0 0 24 24" style="enable-background:new 0 0 50 50;" xml:space="preserve"><rect x="0" y="0" width="4" height="7" fill="#333"><animateTransform  attributeType="xml" attributeName="transform" type="scale" values="1,1; 1,3; 1,1" begin="0s" dur="0.6s" repeatCount="indefinite" /></rect><rect x="10" y="0" width="4" height="7" fill="#333"> <animateTransform  attributeType="xml" attributeName="transform" type="scale" values="1,1; 1,3; 1,1" begin="0.2s" dur="0.6s" repeatCount="indefinite" /></rect><rect x="20" y="0" width="4" height="7" fill="#333"><animateTransform  attributeType="xml" attributeName="transform" type="scale" values="1,1; 1,3; 1,1" begin="0.4s" dur="0.6s" repeatCount="indefinite" /></rect></svg>'} />
</div>
        </div>
      );
    }
  }
}

export default BlogPost;


